}

if (rules[l].giftList[m].orderRatioPrice) { // 如果为换购品，用户未修改购物车数量，则取可换购最大值，用户修改过，则取min(修改过后的数值，可换购最大值)
rules[l].giftList[m].shopCartNum = availableQtyBefore === rules[l].giftList[m].shopCartNum ? rules[l].giftList[m].maxQuotaQty : Math.min(rules[l].giftList[m].maxQuotaQty, rules[l].giftList[m].shopCartNum)
} else { // 如果为赠品
