}).length
if (activeLen) {
let choseLen = rules.filter(i => { // 筛选是否有用户操作赠送/换购品的促销
return i.chose === 2 || i.chose === 3
}).length
if (choseLen === 0) { // 如果用户没有操作，则都是系统选择的，对此做最优方案选择
let byMoneyNum = rules.filter(i => { // 计算配置为满金额的规则的条数
return i.byMoney
}).length
let maxIndex = 0
if (byMoneyNum === 0) { // 如果规则都为满数量，选择符合条件中条件数量最多的
rules.forEach((item, index, arr) => {
if (item.active) {
maxIndex = item.num > arr[maxIndex].num ? index : maxIndex
}
})
} else if (byMoneyNum === rules.length) { // 如果规则都为满金额，选择符合条件中金额最多的
rules.forEach((item, index, arr) => {
if (item.active) {
maxIndex = item.sum > arr[maxIndex].sum ? index : maxIndex
}
})
} else { // 如果不统一，取第一条符合条件的
for (let i = 0; i < rules.length; i++) {
