},
watchTotalPriceExclude (type) { // 主产品总金额决定满单促销是否生效
this.totalSelected = this.productSelected && this.promotionSelected
this.totalPriceExclude = Math.round((this.pTotalPriceExclude + this.productTotalPrice) * 100) / 100
let fullGiftPrice = 0 
for (let i = 0; i < this.promotion.length; i++) {
