}
}

this.fullGiftPrice = fullGiftPrice
this.totalPrice = Math.round((this.pTotalPrice + this.productTotalPrice + this.fullGiftPrice) * 100) / 100
this.saveCart({
type: type,
item: type === 3 ? {product: this.product, promotion: this.promotion} : this.promotion 
})
},
loadQuota (index, j) {
if (this.inited) {
return 
}
this.inited = true
this.$tupHttp(this, `front/product/selectProductQuotaFromSolr?search=${this.promotion[index].promotionRuleVO.productList[j].pCode}`).then(res => {
this.inited = false
this.promotion[index].promotionRuleVO.productList[j].mustQty = res.data.model && res.data.model.mustQty ? res.data.model.mustQty : null
this.promotion[index].promotionRuleVO.productList[j].mqq = res.data.model && res.data.model.mqq ? res.data.model.mqq : null
this.promotion[index].promotionRuleVO.productList[j].availableQty = res.data.model && res.data.model.availableQty !== null ? res.data.model.availableQty : null
this.promotion[index].isNotmm = this.promotion[index].promotionRuleVO.productList.some(childItem => {
return childItem.shopCartNum < childItem.mustQty || childItem.shopCartNum < childItem.mqq || (childItem.availableQty !== null && childItem.shopCartNum > childItem.availableQty)
})
this.$vux.toast.text('配置刷新成功')
this.saveCart({
type: 1,
item: this.promotion
})
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
checkPromotions () {
let arr = this.promotion.map(item => {
return {
id: item.id,
version: item.version,
promotionId: item.promotionId
}
})
if (!arr.length) {
this.checkProductDownAndLack()
return
}
this.$tupHttp(this, 'front/promotion/checkPromotions', 'POST', arr).then(res => {
if (res.data.model.deletes.length) { // 如果有被删除的促销
let promotion = this.$util.deepClone(this.promotion)
res.data.model.deletes.forEach(item => {
promotion = promotion.filter(i => {
return item.id !== i.id
})
})
this.promotion = promotion
}
if (res.data.model.updates.length) { // 如果有被修改的促销
res.data.model.updates.forEach(item => {
for (let i = 0; i < this.promotion.length; i++) {
if (item.id === this.promotion[i].id) {
item.promotionRuleVO.productList && item.promotionRuleVO.productList.map(childItem => {
if (childItem.isHave) {
childItem.selected = true
}
if (childItem.mustQty && item.type !== 4) {
childItem.selected = true
childItem.shopCartNum = childItem.mustQty
}
if (item.type === 4) {
childItem.selected = true
childItem.shopCartNum = childItem.productNum
}
return childItem
})
item.selected = true
item.shopCartNum = 1
item.ifEdit = false
item.keyProductPrice = 0
item.promotionTotalPrice = 0
this.promotion[i] = item
}
}
})
this.$router.replace({
path: '/refresh',
query: {
t: Date.now()
}
})
}
this.checkProductDownAndLack()
})
},
checkProductDownAndLack () {
let down = []
let lack = []
down = this.product.filter(i => {
return i.pStatus === 2
}).map(i => {
return i.pCode
})
lack = this.product.filter(i => {
return !i.isHave
}).map(i => {
return i.pCode
})
this.promotion.forEach((item) => {
item.promotionRuleVO.productList && item.promotionRuleVO.productList.forEach((sItem) => {
if (sItem.pStatus === 2 && down.indexOf(sItem.pCode) < 0) {
down.push(sItem.pCode)
}
if (!sItem.isHave && lack.indexOf(sItem.pCode) < 0) {
lack.push(sItem.pCode)
}
})
if (item.type !== 0) { // 优惠促销没有rules
item.promotionRuleVO.rules.forEach(sItem => {
sItem.giftList.forEach(xsItem => {
if (xsItem.pStatus === 2 && down.indexOf(xsItem.pCode) < 0) {
down.push(xsItem.pCode)
}
if (!xsItem.isHave && lack.indexOf(xsItem.pCode) < 0) {
lack.push(xsItem.pCode)
}
})
})
}
})
if (lack.length || down.length) {
this.$tupHttp(this, 'front/product/checkProductDownAndLack', 'POST', {
inStockProductCode: lack,
upProductCode: down
}).then(res => {
let data = res.data.model
if (data.inStockProductCode.length) {
this.product = this.product.map(item => {
if (data.inStockProductCode.indexOf(item.pCode) >= 0) {
item.isHave = 1
}
return item
})
this.promotion = this.promotion.map(item => {
item.promotionRuleVO.productList = item.promotionRuleVO.productList.map(sItem => {
if (data.inStockProductCode.indexOf(sItem.pCode) >= 0) {
sItem.isHave = 1
}
return sItem
})
if (item.type !== 0) {
item.promotionRuleVO.rules = item.promotionRuleVO.rules.map(sItem => {
sItem.giftList = sItem.giftList.map(xsItem => {
if (data.inStockProductCode.indexOf(xsItem.pCode) >= 0) {
xsItem.isHave = 1
}
return xsItem
})
return sItem
})
}
return item
})
}
if (data.upProductCode.length) {
this.product = this.product.map(item => {
if (data.upProductCode.indexOf(item.pCode) >= 0) {
item.pStatus = 1
}
return item
})
this.promotion = this.promotion.map(item => {
item.promotionRuleVO.productList = item.promotionRuleVO.productList.map(sItem => {
if (data.upProductCode.indexOf(sItem.pCode) >= 0) {
sItem.pStatus = 1
}
return sItem
})
if (item.type !== 0) {
item.promotionRuleVO.rules = item.promotionRuleVO.rules.map(sItem => {
sItem.giftList = sItem.giftList.map(xsItem => {
if (data.upProductCode.indexOf(xsItem.pCode) >= 0) {
xsItem.pStatus = 1
}
return xsItem
})
return sItem
})
}
return item
})
}
this._initPromotion()
})
} else {
this._initPromotion()
}
},
numberClick (e) {
switch (e.target.nodeName) {
case 'path':
if (e.target.parentNode.parentNode.className.search('vux-number-selector-plus vux-number-disabled') >= 0) {
this.$vux.toast.text('配额不足')
}
break
case 'svg':
if (e.target.parentNode.className.search('vux-number-selector-plus vux-number-disabled') >= 0) {
this.$vux.toast.text('配额不足')
}
break
case 'a':
if (e.target.className.search('vux-number-selector-plus vux-number-disabled') >= 0) {
this.$vux.toast.text('配额不足')
}
break
}
},
toEdit () {
this.promotion = this.promotion.map(item => {
item.ifEdit = false
return item
})
this.product = this.product.map(item => {
item.ifEdit = false
return item
})
this.ifEdit = !this.ifEdit
this.productIfEdit = false
this.totalEditSelected = false
},
productEditClick (index) { // 编辑时勾选常规产品小类
this.product[index].ifEdit = !this.product[index].ifEdit
this.productIfEdit = this.product.every(item => {
return item.ifEdit
})
let promotionTotalEdit = !this.promotion.some(item => { // 是否有非必订非满单的促销没被勾选上
return (item.type === 0 || item.type === 1 || item.type === 4) && !item.isMust && !item.ifEdit
})
this.totalEditSelected = this.productIfEdit && promotionTotalEdit
},
promotionEditClick (index) { // 编辑时勾选促销产品小类
this.promotion[index].ifEdit = !this.promotion[index].ifEdit
let promotionTotalEdit = !this.promotion.some(item => { // 是否有非必订非满单的促销没被勾选上
return (item.type === 0 || item.type === 1 || item.type === 4) && !item.isMust && !item.ifEdit
})
this.totalEditSelected = this.productIfEdit && promotionTotalEdit
},







totalEditClick () { // 编辑时点击全选
let val = !this.totalEditSelected
this.promotion = this.promotion.map(item => {
item.ifEdit = val
return item
})
this.product = this.product.map(item => {
item.ifEdit = val
return item
})
this.productIfEdit = val
},
delEdit () { // 编辑时点击删除
let productEdit = this.product.some(item => {
return item.ifEdit
})
let promotionEdit = this.promotion.some(item => {
return item.ifEdit
})
if (productEdit || promotionEdit) {
this.ifRemove = true
} else {
this.$vux.toast.text('请勾选需要删除的产品~')
}
},
onConfirm () {
for (let i = 0; i < this.product.length; i++) {
if (this.product[i].ifEdit && !this.product[i].mustQty) {
this.product.splice(i, 1)
i--
}
}
for (let i = 0; i < this.promotion.length; i++) {
if ((this.promotion[i].type === 0 || this.promotion[i].type === 1 || this.promotion[i].type === 4) && !this.promotion[i].isMust && this.promotion[i].ifEdit) {
this.promotion.splice(i, 1)
i--
}
}
this.watchProduct()
this.watchPromotion()
},
productClick (index) { // 下单时勾选常规产品
this.product[index].selected = !this.product[index].selected
this.watchProduct()
},
changeProductNum (index) { // 下单时修改常规产品数量
if (this.product[index].mqq && this.product[index].shopCartNum < this.product[index].mqq) {
this.product[index].shopCartNum = 0
}
this.watchProduct()
},
promotionCheck (index) {
if (this.promotion[index].isMust) { // 必定大礼包促销不能切换选择
return
}
this.promotion[index].selected = !this.promotion[index].selected
for (let j = 0; j < this.promotion[index].promotionRuleVO.productList.length; j++) {
this.promotion[index].promotionRuleVO.productList[j].selected = this.promotion[index].selected
}
this.watchPromotion()
},
promotionClick (index, j) { // 下单时勾选促销产品
this.promotion[index].promotionRuleVO.productList[j].selected = !this.promotion[index].promotionRuleVO.productList[j].selected
this.watchPromotion()
},
changePromotionNum (i, j) { // 下单时修改促销产品数量
if (typeof i === 'number' && typeof j === 'number' && this.promotion[i].promotionRuleVO.productList[j].mqq && this.promotion[i].promotionRuleVO.productList[j].mqq > this.promotion[i].promotionRuleVO.productList[j].shopCartNum) {
this.promotion[i].promotionRuleVO.productList[j].shopCartNum = 0
}
this.watchPromotion()
},
totalClick () { // 点击全选
let val = !this.totalSelected 
let product = this.product
let promotion = this.promotion
for (let i = 0, len = product.length; i < len; i++) {
if (product[i].isHave && product[i].pStatus !== 2 && !product[i].mustQty) { // 如果产品有货，上架并且不必订
product[i].selected = val
}
}
for (let j = 0, len = promotion.length; j < len; j++) {
if (promotion[j].type === 4 && !promotion[j].isMust) { // 非必定首订大礼包selected跟着全选变动
promotion[j].selected = val
}
for (let k = 0; k < promotion[j].promotionRuleVO.productList.length; k++) {
if (promotion[j].type !== 4 || (promotion[j].type === 4 && !promotion[j].isMust)) {
if (promotion[j].promotionRuleVO.productList[k].isHave && promotion[j].promotionRuleVO.productList[k].pStatus !== 2 && !promotion[j].promotionRuleVO.productList[k].mustQty) {
promotion[j].promotionRuleVO.productList[k].selected = val
}
}
}
}
this.product = product
this.promotion = promotion
this.watchProduct()
this.watchPromotion()
},
showZengPop () {
this.needGift = this.needGiftProduct
this.ifZengPop = true
},
zengPopClick () {
this.needGift = this.needGift === 1 ? 0 : 1
},
closeZengPop () {
this.ifZengPop = false
},
confirmZeng () {
if (this.needGiftProduct !== this.needGift) {
this.needGiftProduct = this.needGift
let cart = localStore.get('TUP_cart')
cart.needGiftProduct = this.needGiftProduct
localStore.set('TUP_cart', cart) 
}
this.ifZengPop = false
},
showPop (item, index) {
this.ifPop = true
this.popupContent = this.$util.deepClone(item)
this.popupIndex = index
},
zengClick (index, i) { // 点击弹窗选择按钮，为了监听用户手动修改了赠品/选购内容
let promotionRuleVO = this.popupContent.promotionRuleVO 
if (!promotionRuleVO.rules[index].active) {
return
}
let ruleItem = promotionRuleVO.rules[index]
ruleItem.giftList[i].selected = !ruleItem.giftList[i].selected
let len = ruleItem.giftList.filter(j => { // 该规则选中的个数
return j.selected
}).length
if (len === 0) { // 为0则该规则全部取消勾选，chose状态为2
ruleItem.chose = 2
} else { // 该规则仍有选中的赠品，chose状态为3
ruleItem.chose = 3
}
if (promotionRuleVO.relation === 1 && ruleItem.chose === 3) { // 如果是互斥规则，选中其中一个，其他要取消勾选
for (let m = 0; m < promotionRuleVO.rules.length; m++) {
if (m !== index) {
promotionRuleVO.rules[m].chose = 0
promotionRuleVO.rules[m].giftList.forEach((item) => {
item.selected = false
})
}
}
}
},
confirm () {
this.ifPop = false
this.promotion[this.popupIndex] = this.popupContent
this.watchPromotion()
},
closePop () {
this.ifPop = false
},
toConfirm () {
if (this.inited) {
return 
}
if (!this.totalPrice) {
this.$vux.toast.text('请选择产品~')
return
}
let isNotmm = this.promotion.some(item => {
return item.isNotmm
})
if (isNotmm) {
this.$vux.toast.text('首订礼包有产品必订量，起订量或者配额配置有误~')
return
}
this.inited = true
this.$tupHttp(this, 'front/order/order/checkOrderPlan', 'POST', {
bookType: this.pFirstOrderDate ? 0 : 1, // 0普订1首订
timeType: 1 
}).then(res => {
if (res.data.model) {
this.$tupHttp(this, 'front/systemParameters/get', 'POST').then(res => { // 下单需满金额
let leastSum = this.pFirstOrderDate ? res.data.model.pMinorderamount : res.data.model.firstOrderMinimum
if (this.totalPrice < leastSum) {
this.$vux.toast.text('订单需满' + leastSum + '才可提交')
this.inited = false
} else {
this.$router.push({
path: '/orderConfirm'
})
}
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
} else {
this.$vux.toast.text('当前不是下单时间~')
this.inited = false
}
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
},
_initPromotion () {
if (this.product.length || this.promotion.length) {
this.watchProduct()
this.watchPromotion()
} else {
this.ifNull = false
}
},
...mapActions([
'setDot',
'saveCart'
])
},
created () {
this.product = localStore.get('TUP_cart').product
this.promotion = localStore.get('TUP_cart').promotion
this.giftProducts = localStore.get('TUP_cart').giftProducts
this.pFirstOrderDate = localStore.get('TUP_pFirstorderDate')
this.$tupHttp(this, 'front/order/order/checkOrderPlan', 'POST', {
bookType: this.pFirstOrderDate ? 0 : 1, // 0普订1首订
timeType: 0 
}).then(res => {
if (res.data.model) {
this.checkPromotions()
} else {
this.ifNotTime = true
}
})
},
components: {
Group,
XButton,
XHeader,
CheckIcon,
XNumber,
Scroll,
Popup,
Confirm
}
}



